import { Redirect, Route, Switch } from 'react-router-dom';

import Header from './components/Header/Header';
import { PAGES } from './constants/pages';

import 'antd/dist/antd.css';
import './App.css';

function App() {
  return (
    <div className="App">
      <Header />
      <Switch>
        {PAGES.filter((item) => !!item.component).map((item) => {
          const PageComponent = item.component;
          return (
            <Route path={item.path} exact={!!item.component} key={item.id}>
              <PageComponent />
            </Route>
          );
        })}
        <Redirect to="/about" />
      </Switch>
    </div>
  );
}

export default App;
